@import '../../../assets/css/theme';
.warning_img {
  img {
    width: 80px;
    height: 80px;
  }
  text-align: center;
}
.box {
  font-family: PingFangSC-Semibold, PingFang SC;
  background: $color-icon-warning-1;
  border-radius: 5px;
  border: 1px solid $color-icon-warning-3;
  color: $color-warning;
  padding: 10px;
  text-align: center;
  line-height: 30px;
  margin: 20px 0;
}
.checkbox_info {
  font-family: PingFangSC-Regular, PingFang SC;
  color: $color-warning;
}
