@import './var.scss';
@import './theme';
@import 'mixins';

.tempalte-pageTab-nav {
  display: flex;
  overflow: hidden;
  border-radius: 30px 30px 0 0;
  @include mobile-only {
    border-radius: 10px 10px 0 0;
  }
  align-items: flex-end;
  .tempalte-pageTab-nav-item {
    position: relative;
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    height: 60px;
    font-size: 20px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    background: $color-card-bg-default;
    color: $color-text-default-5;
    cursor: pointer;
    &:first-child {
      border-top-left-radius: 20px;
      @include mobile-only {
        border-top-left-radius: 10px;
      }
    }
    &:last-child {
      border-top-right-radius: 20px;
      @include mobile-only {
        border-top-right-radius: 10px;
      }
    }
  }
  .tempalte-pageTab-nav-active {
    background-color: $color-card-bg-active;
    color: $color-text-default;
    height: 70px;
    font-size: 30px;
    &::before {
      content: '';
      position: absolute;
      left: -70px;
      top: 0;
      width: 70px;
      height: 100%;
      @include darkThemeOnly {
        background: url(../images/common_tab_left.png) no-repeat;
        background-size: 100% 100%;
      }
      @include lightThemeOnly {
        background: url(../images/common_tab_left_light.png) no-repeat;
        background-size: 100% 100%;
      }
    }
    &::after {
      z-index: 2;
      content: '';
      position: absolute;
      right: -70px;
      top: 0;
      width: 70px;
      height: 100%;
      @include darkThemeOnly {
        background: url(../images/common_tab_right.png) no-repeat;
        background-size: 100% 100%;
      }
      @include lightThemeOnly {
        background: url(../images/common_tab_right_light.png) no-repeat;
        background-size: 100% 100%;
      }
    }
  }
}

@include mobile-only {
  .tempalte-pageTab-nav {
    .tempalte-pageTab-nav-item {
      height: 35px;
      font-size: 14px;
    }

    .tempalte-pageTab-nav-active {
      height: 40px;
      font-size: 18px;
      &::before {
        left: -40px;
        width: 40px;
      }
      &::after {
        right: -40px;
        width: 40px;
      }
    }
  }
}
